import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../Ui/Border/BorderBottom";
import FormToggle from "../../../StaffTab/components/DividerTab/MainContent/StaffForm/FormToggle";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import NotifyEvent from "../../Utils/Snackbar/NotifyEvent";
import {
  useLmsDashboardMaster,
  useLmsLeaveController,
} from "../../Hooks/ApiHooks/LmsManager/lms-manager-api";
const InstituteLmsLeaveTabController = ({ lmid }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    bereavement_leave: true,
    c_off_leave: true,
    casual_leave: true,
    child_adoption_leave: true,
    commuted_leave: true,
    half_pay_leave: true,
    leave_taken: true,
    lwp_leave: true,
    maternity_leave: true,
    medical_leave: true,
    off_duty_leave: true,
    paternity_leave: true,
    quarantine_leave: true,
    sabbatical_leave: true,
    sick_leave: true,
    special_disability_leave: true,
    study_leave: true,
    summer_vacation_leave: true,
    winter_vacation_leave: true,
  });
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const [lmsLeaveController] = useLmsLeaveController();
  const { lmsDashboardMaster } = useLmsDashboardMaster({
    data: {
      lmid: lmid,
      mod_id: "",
    },
    skip: !lmid,
  });

  useEffect(() => {
    if (lmsDashboardMaster?.leave_manage) {
      setState({
        bereavement_leave: lmsDashboardMaster?.leave_manage?.bereavement_leave,
        c_off_leave: lmsDashboardMaster?.leave_manage?.c_off_leave,
        casual_leave: lmsDashboardMaster?.leave_manage?.casual_leave,
        child_adoption_leave:
          lmsDashboardMaster?.leave_manage?.child_adoption_leave,
        commuted_leave: lmsDashboardMaster?.leave_manage?.commuted_leave,
        half_pay_leave: lmsDashboardMaster?.leave_manage?.half_pay_leave,
        leave_taken: lmsDashboardMaster?.leave_manage?.leave_taken,
        lwp_leave: lmsDashboardMaster?.leave_manage?.lwp_leave,
        maternity_leave: lmsDashboardMaster?.leave_manage?.maternity_leave,
        medical_leave: lmsDashboardMaster?.leave_manage?.medical_leave,
        off_duty_leave: lmsDashboardMaster?.leave_manage?.off_duty_leave,
        paternity_leave: lmsDashboardMaster?.leave_manage?.paternity_leave,
        quarantine_leave: lmsDashboardMaster?.leave_manage?.quarantine_leave,
        sabbatical_leave: lmsDashboardMaster?.leave_manage?.sabbatical_leave,
        sick_leave: lmsDashboardMaster?.leave_manage?.sick_leave,
        special_disability_leave:
          lmsDashboardMaster?.leave_manage?.special_disability_leave,
        study_leave: lmsDashboardMaster?.leave_manage?.study_leave,
        summer_vacation_leave:
          lmsDashboardMaster?.leave_manage?.summer_vacation_leave,
        winter_vacation_leave:
          lmsDashboardMaster?.leave_manage?.winter_vacation_leave,
      });
    }
  }, [lmsDashboardMaster?.leave_manage]);

  const onSubmitFormHandler = (field, value) => {
    if (lmid) {
      const updatedObj = { ...state };
      for (let obj in updatedObj) {
        if (obj === field) {
          updatedObj[obj] = value;
        }
      }
      lmsLeaveController({
        lmid: lmid,
        leaveManager: { leave_manage: updatedObj },
      })
        .then(() => {
          setSnackbar({
            msg: "Updated",
            open: true,
            variant: "success",
          });
        })
        .catch({});
      setState((prev) => ({
        ...prev,
        [field]: value,
      }));
    }
  };

  return (
    <>
      <div className={style.fm_search}>
        <FormToggle
          labelHeading={t("casual_leave")}
          checkedToggle={state.casual_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("casual_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />
        <FormToggle
          labelHeading={t("medical_leave")}
          checkedToggle={state.medical_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("medical_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />

        <FormToggle
          labelHeading={t("sick_leave")}
          checkedToggle={state.sick_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("sick_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />
      </div>
      <BorderBottom />
      <div className={style.fm_search}>
        <FormToggle
          labelHeading={t("c_off_leave")}
          checkedToggle={state.c_off_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("c_off_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />

        <FormToggle
          labelHeading={t("on_duty_leave")}
          checkedToggle={state.off_duty_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("off_duty_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />

        <FormToggle
          labelHeading={t("leave_without_pay")}
          checkedToggle={state.lwp_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("lwp_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />
      </div>
      <BorderBottom />
      <div className={style.fm_search}>
        <FormToggle
          labelHeading={t("commuted_leave")}
          checkedToggle={state.commuted_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("commuted_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />

        <FormToggle
          labelHeading={t("maternity_leave")}
          checkedToggle={state.maternity_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("maternity_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />

        <FormToggle
          labelHeading={t("paternity_leave")}
          checkedToggle={state.paternity_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("paternity_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />
      </div>
      <BorderBottom />
      <div className={style.fm_search}>
        <FormToggle
          labelHeading={t("quarantine_leave")}
          checkedToggle={state.quarantine_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("quarantine_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />
        <FormToggle
          labelHeading={t("half_pay_leave")}
          checkedToggle={state.half_pay_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("half_pay_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />
        <FormToggle
          labelHeading={t("study_leave")}
          checkedToggle={state.study_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("study_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />
      </div>
      <BorderBottom />

      <div className={style.fm_search}>
        <FormToggle
          labelHeading={t("sabbatical_leave")}
          checkedToggle={state.sabbatical_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("sabbatical_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />
        <FormToggle
          labelHeading={t("special_disability_leave")}
          checkedToggle={state.special_disability_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("special_disability_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />

        <FormToggle
          labelHeading={t("winter_vacation_leave")}
          checkedToggle={state.winter_vacation_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("winter_vacation_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />
      </div>

      <div className={style.fm_search}>
        <FormToggle
          labelHeading={t("summer_vacation_leave")}
          checkedToggle={state.summer_vacation_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("summer_vacation_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />
        <FormToggle
          labelHeading={t("child_adoption_leave")}
          checkedToggle={state.child_adoption_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("child_adoption_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />

        <FormToggle
          labelHeading={t("bereavement_leave")}
          checkedToggle={state.bereavement_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("bereavement_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />
      </div>
      <div className={style.fm_search}>
        <FormToggle
          labelHeading={t("earned_leave")}
          checkedToggle={state.earned_leave}
          onToggleHandler={(e) =>
            onSubmitFormHandler("earned_leave", e.target.checked)
          }
          customContainer={{
            flex: "1 1 33%",
          }}
        />
      </div>

      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default InstituteLmsLeaveTabController;
